:root {

    --main-color: #764095;
    --second-color: #8b58a9;
  }

/* start carousel */

.carousel {
    max-width: 1200px;
    width: 100%;
  }
  
  .slider-wrapper {
    position: relative;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.12);
  }
  
  .slider-wrapper .slide-button {
    position: absolute;
    top: 50%;
    outline: none;
    border: none;
    height: 50px;
    width: 50px;
    z-index: 5;
    color: #fff;
    display: flex;
    cursor: pointer;
    font-size: 2.2rem;
    background: var(--main-color);
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transform: translateY(-50%);
  }
  
  .slider-wrapper .slide-button:hover {
    background: #404040;
  }
  
  .slider-wrapper .slide-button#prev-slide {
    left: -25px;
  }
  
  .slider-wrapper .slide-button#next-slide {
    right: -25px;
  }
  
  .slider-wrapper .image-list {
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    gap: 18px;
    font-size: 0;
    list-style: none;
    margin-bottom: 30px;
    overflow-x: auto;
    scrollbar-width: none;
  }
  
  .slider-wrapper .image-list::-webkit-scrollbar {
    display: none;
  }
  
  .slider-wrapper .image-list .image-item {
    width: 260px;
    height: 260px;
    object-fit: cover;
  }
  
  .carousel .slider-scrollbar {
    height: 24px;
    width: 100%;
    display: none;
    align-items: center;
  }
  
  .slider-scrollbar .scrollbar-track {
    background: #ccc;
    width: 100%;
    height: 2px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    position: relative;
  }
  
  .slider-scrollbar:hover .scrollbar-track {
    height: 4px;
  }
  
  .slider-scrollbar .scrollbar-thumb {
    position: absolute;
    background: #000;
    top: 0;
    bottom: 0;
    width: 50%;
    height: 100%;
    cursor: grab;
    border-radius: inherit;
  }
  
  .slider-scrollbar .scrollbar-thumb:active {
    cursor: grabbing;
    height: 8px;
    top: -2px;
  }
  
  .slider-scrollbar .scrollbar-thumb::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: -10px;
    bottom: -10px;
  }
  
  /* Styles for mobile and tablets */
  @media only screen and (max-width: 1023px) {
    .slider-wrapper .image-list {
      gap: 10px;
      margin-bottom: 15px;
      scroll-snap-type: x mandatory;
    }
    .slider-wrapper .image-list .custom-card {
      width: 11rem;
    }
    .slider-wrapper .image-list .custom-card .card-body {
      padding: 1.1rem;
    }
  
    .slider-wrapper .image-list .image-item {
      width: 280px;
      height: 380px;
    }
  
    .slider-scrollbar .scrollbar-thumb {
      width: 20%;
    }
    .slider-wrapper .slide-button {
      /* display: none; */
    }
  }
  
  .fa-solid,
  .fas {
    font-weight: 900;
    color: rgb(235, 32, 39);
  }
  

@media screen and (max-width:730px) {
  
.carousel {
  width: 95%;
}
.slider-wrapper .slide-button#prev-slide {
  left: -15px;
}

.slider-wrapper .slide-button#next-slide {
  right: -15px;
}
.slider-wrapper .slide-button {
  height: 40px;
  width: 40px;
  font-size: 2rem;

}

}


  /* end carousel */


.latest{
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.latest .container{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}
.latest .container .title{
    font-size: 25px;
    color: var(--main-color);
}
/* .latest .container .toggler{

} */


.latest .container .toggler {
    background-color: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    max-width: 90%;
  }
  .latest .container .toggler button {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: var(--main-color);
    padding: 10px 25px;
    font-size: 23px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    transition: background-color all 0.6s;
    -webkit-transition: background-color all 0.6s;
    -moz-transition: background-color all 0.6s;
    -ms-transition: background-color all 0.6s;
    -o-transition: background-color all 0.6s;
  }
  .latest .container .toggler button.active {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #fff;
    background: var(--main-color);
    padding: 10px 25px;
    transition: all 0.6s;
    font-size: 20px;
    cursor: pointer;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
  }

/* ---------------- */
