
#drop-menu{
    max-height: 78vh;
    overflow: scroll;
}


@media screen and (max-width:470px) {
  
#nav{
    padding: 0px 0px 0 5px !important;
}

  
    h4:where(.css-dev-only-do-not-override-zg0ahe).ant-typography,
    div:where(.css-dev-only-do-not-override-zg0ahe).ant-typography-h4,
    div:where(.css-dev-only-do-not-override-zg0ahe).ant-typography-h4 > textarea,
    :where(.css-dev-only-do-not-override-zg0ahe).ant-typography h4 {
      margin-bottom: 0.5em;
      color: rgba(0, 0, 0, 0.88);
      font-weight: 600;
      font-size: 17px;
      line-height: 1.4;
    }
    }
@media screen and (max-width:570px) {
  
    #drop-menu{
        width: 280px !important;
        /* inset: 52px 25px auto auto !important; */
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-63%, 0%);
        max-height: 78vh;
        overflow: scroll;
        }
          
    }

  